import * as React from 'react';
import { connect } from 'react-redux';
import { StickyContainer, Sticky } from 'react-sticky';
import { setUserData } from '../../actions/action';
import { ListView, Button, ActionSheet } from 'antd-mobile';
import './ServicesMobile.css';
import TripDTO, { ServiceTargetLocationType } from '../../dto/TripDTO';
import { WithCommittee, WithConnector, WithUser } from '../../reducers/reducers';
import { WeekDays } from '../../utils/Constants';
import Tag from 'antd/lib/tag';
import { VehicleType } from '../../dto/VehicleDTO';

interface ServicesMobileState {
  dataSource: any;
}

interface ServicesMobileProps extends WithUser, WithConnector, WithCommittee {
  trips: TripDTO[];
  pullToRefresh: React.ReactNode;
}

class ServicesMobile extends React.Component<ServicesMobileProps, ServicesMobileState> {
  lv: any;

  constructor(props: any) {
    super(props);
    const getSectionData = (dataBlob: any, sectionID: any) => dataBlob[sectionID];
    const getRowData = (dataBlob: any, sectionID: any, rowID: any) => dataBlob[rowID];

    const myDataBlobs = this.buildDataBlobs(props.trips);
    const { dataBlobs, sectionIDs, rowIDs } = myDataBlobs;
    let dataSource = new ListView.DataSource({
      getRowData,
      getSectionHeaderData: getSectionData,
      rowHasChanged: (row1: any, row2: any) => row1 !== row2,
      sectionHeaderHasChanged: (s1: any, s2: any) => s1 !== s2
    });
    dataSource = dataSource.cloneWithRowsAndSections(dataBlobs, sectionIDs, rowIDs);
    this.state = {
      dataSource
    };
  }

  componentWillReceiveProps(nextProps: ServicesMobileProps) {
    const { trips } = nextProps;
    const myDataBlobs = this.buildDataBlobs(trips);
    const { dataBlobs, sectionIDs, rowIDs } = myDataBlobs;
    this.setState({
      dataSource: this.state.dataSource.cloneWithRowsAndSections(dataBlobs, sectionIDs, rowIDs)
    });
  }

  private buildDataBlobs = (trips: TripDTO[]): { dataBlobs: any; sectionIDs: any; rowIDs: any } => {
    let dataBlobs: any = {};
    let sectionIDs: any = [];
    let rowIDs: any = [];
    if (!trips || trips.length === 0) {
      return { dataBlobs, sectionIDs, rowIDs };
    }
    const grouped: any = this.groupBy(trips, 'date');
    const sections: string[] = Object.keys(grouped);
    for (let i = 0; i < sections.length; i++) {
      const date = sections[i];
      sectionIDs.push(date);
      dataBlobs[date] = date;
      rowIDs[i] = [];

      const tripsOfDate: TripDTO[] = grouped[date];
      for (let j = 0; j < tripsOfDate.length; j++) {
        const tripOfDate: TripDTO = tripsOfDate[j];
        const tripId = tripOfDate.id;
        rowIDs[i].push(tripId);
        dataBlobs[tripId] = tripId;
      }
    }
    sectionIDs = [...sectionIDs];
    rowIDs = [...rowIDs];
    return {
      dataBlobs,
      sectionIDs,
      rowIDs
    };
  };

  private groupBy = (trips: TripDTO[], key: string) => {
    if (trips.length === 0) {
      return {};
    }
    return trips.reduce((rv: any, x: any) => {
      (rv[x[key]] = rv[x[key]] || []).push(x);
      return rv;
    }, {});
  };

  private renderSeparator = (sectionID: string | number, rowID: string | number, adjacentRowHighlighted?: boolean): React.ReactElement<any> => (
    <div
      key={`${sectionID}-${rowID}`}
      style={{
        backgroundColor: '#F5F5F9',
        height: 15,
        borderTop: '1px solid #ECECED',
        borderBottom: '1px solid #ECECED'
      }}
    />
  );

  private renderSectionWrapper = (): React.ReactElement<any> => <StickyContainer key={`s_boooo_c`} className="sticky-container" style={{ zIndex: 4 }} />;

  private renderSectionHeader = (sectionData: string, sectionId: string | number): React.ReactElement<any> => {
    const date = new Date(sectionData);
    const weekDay: string = WeekDays[date.getDay()];
    const formattedDate = [this.pad(date.getDate()), this.pad(date.getMonth() + 1), date.getFullYear()].join('/');
    return (
      <Sticky>
        {({ style }) => (
          <div
            className="sticky"
            style={{
              ...style,
              marginTop: '45px',
              zIndex: 4,
              backgroundColor: '#F8591A' /* '#5890ff', */,
              color: 'white'
            }}
          >
            {formattedDate} - {weekDay}
          </div>
        )}
      </Sticky>
    );
  };

  private pad = (s: any) => {
    return s < 10 ? '0' + s : s;
  };

  private renderRow = (rowData: any, sectionID: string | number, rowID: string | number, highlightRow?: boolean): React.ReactElement<any> => {
    const { trips } = this.props;
    const trip: TripDTO | undefined = trips.find((x) => x.id === rowID);
    if (trip !== undefined) {
      const { vehicle } = trip;
      return (
        <div key={rowID} style={{ padding: '0 15px' }}>
          <div
            style={{
              textAlign: 'right',
              borderBottom: '2px solid #F6F6F6'
            }}
          >
            <Tag style={{ marginLeft: '5px' }} color="green">
              {ServiceTargetLocationType[trip.flgDestination]}
            </Tag>{' '}
            <b>{trip.hour.substr(0, 5)}</b>
          </div>
          <div
            style={{
              textAlign: 'left',
              borderBottom: '2px solid #F6F6F6'
            }}
          >
            <b>Partenza:</b> {trip.siteDeparture}
          </div>
          <div
            style={{
              textAlign: 'left',
              borderBottom: '2px solid #F6F6F6'
            }}
          >
            <b>Arrivo:</b> {trip.siteArrival}
          </div>
          <div
            style={{
              textAlign: 'left',
              borderBottom: '2px solid #F6F6F6'
            }}
          >
            <b>Veicolo: </b> {!!vehicle ? `${VehicleType[vehicle.type]} - ${vehicle.sign} - ${vehicle.description}` : '-'}
          </div>
          <div
            style={{
              textAlign: 'left',
              borderBottom: '2px solid #F6F6F6'
            }}
          >
            <div>
              <b>Servizio:</b> {trip.serviceType}
            </div>
            <div>
              <b>Note:</b> {trip.notes}
            </div>
            <Button type="primary" icon="check-circle-o" size="small" onClick={this.showActionSheet}>
              Prenota
            </Button>
          </div>
        </div>
      );
    } else {
      return <div>Data not loaded!</div>;
    }
  };

  private showActionSheet = () => {
    const BUTTONS = ['Autista', 'Volontario', 'Medico', 'Annulla'];
    ActionSheet.showActionSheetWithOptions(
      {
        options: BUTTONS,
        destructiveButtonIndex: BUTTONS.length - 1,
        message: 'Seleziona il ruolo',
        maskClosable: true
      },
      (index: number, rowIndex?: number) => {
        console.log(`index ${index} - rowIndex ${rowIndex}`);
      }
    );
  };

  private renderFooter = (): React.ReactElement<any> => (
    <div></div>
    // <div style={{ padding: 30, textAlign: 'center' }}>{this.state.isLoading ? 'Caricamento in corso...' : ''}</div>
  );

  public render() {
    const { dataSource } = this.state;
    return (
      <ListView
        ref={(el) => (this.lv = el)}
        dataSource={dataSource}
        className="am-list sticky-list"
        useBodyScroll
        renderSectionWrapper={this.renderSectionWrapper}
        renderSectionHeader={this.renderSectionHeader}
        renderFooter={this.renderFooter}
        renderRow={this.renderRow}
        renderSeparator={this.renderSeparator}
        pageSize={4}
        pullToRefresh={this.props.pullToRefresh}
        onScroll={() => {
          console.log('scroll');
        }}
        scrollEventThrottle={200}
      />
    );
  }
}

const mapStateToProps = (state: any) => {
  return {
    user: state.appData.user,
    connector: state.appData.connector,
    committee: state.appData.committee
  };
};

const ServicesMobileContainer = connect(mapStateToProps, {
  setUserData
})(ServicesMobile);
export default ServicesMobileContainer;
