import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { Links } from './utils/Links';
import LoginContainer from './components/common/Login';
import DashboardContainer from './components/desktop/Dashboard';
import DashboardMobileContainer from './components/mobile/DashboardMobile';

class App extends React.Component {
  public render() {
    return (
      <Router>
        <Switch>
          <Route exact={true} path={Links.LOGIN} component={LoginContainer} />
          <Route path={Links.DESKTOP} component={DashboardContainer} />
          <Route path={Links.MOBILE} component={DashboardMobileContainer} />
        </Switch>
      </Router>
    );
  }
}

export default App;
