import CommitteeDTO from '../dto/CommitteeDTO';
import UserDTO from '../dto/UserDTO';

export enum ReduxActionTypes {
  SET_SHOW_LOADING = 'SET_SHOW_LOADING',
  SET_USER_DATA = 'SET_USER_DATA',
  SET_ACCESS_TOKEN = 'SET_ACCESS_TOKEN',
  SET_COMMITTEE_DATA = 'SET_COMMITTEE_DATA',
  RESET_AUTH_DATA = 'RESET_AUTH_DATA',
  SET_USERS_DATA = 'SET_USERS_DATA'
}

export interface ReduxBaseAction {
  type: ReduxActionTypes;
}

export interface ReduxSetUserData extends ReduxBaseAction {
  type: ReduxActionTypes.SET_USER_DATA;
  data: {
    user: UserDTO;
    committeeId: number;
  };
}

export interface ReduxSetAccessToken extends ReduxBaseAction {
  type: ReduxActionTypes.SET_ACCESS_TOKEN;
  data: {
    accessToken: string;
  };
}

export interface ReduxSetCommitteeData extends ReduxBaseAction {
  type: ReduxActionTypes.SET_COMMITTEE_DATA;
  data: {
    committee: CommitteeDTO;
  };
}

export interface ReduxSetUsersData extends ReduxBaseAction {
  type: ReduxActionTypes.SET_USERS_DATA;
  data: {
    users: UserDTO[];
  };
}

export interface ReduxSetShowLoading extends ReduxBaseAction {
  type: ReduxActionTypes.SET_SHOW_LOADING;
  data: boolean;
}

export interface ReduxResetAuthData extends ReduxBaseAction {
  type: ReduxActionTypes.RESET_AUTH_DATA;
}

export type TReducerActions =
  | ReduxSetUserData
  | ReduxSetAccessToken
  | ReduxSetCommitteeData
  | ReduxSetShowLoading
  | ReduxResetAuthData
  | ReduxSetUsersData;

export interface SetUsersDataProps {
  setUsersData(users: UserDTO[]): ReduxSetUsersData;
}

export interface SetUserDataProps {
  setUserData(user: UserDTO, committeeId: number): ReduxSetUserData;
}

export interface SetAccessTokenProps {
  setAccessToken(accessToken: string): ReduxSetAccessToken;
}

export interface SetCommitteeDataProps {
  setCommitteeData(committee: CommitteeDTO): ReduxSetCommitteeData;
}

export interface ResetAuthDataProps {
  resetAuthData(): ReduxResetAuthData;
}

export function setUsersData(users: UserDTO[]): ReduxSetUsersData {
  return {
    type: ReduxActionTypes.SET_USERS_DATA,
    data: {
      users
    }
  };
}

export function setUserData(user: UserDTO, committeeId: number): ReduxSetUserData {
  return {
    type: ReduxActionTypes.SET_USER_DATA,
    data: {
      user,
      committeeId
    }
  };
}

export function setAccessToken(accessToken: string): ReduxSetAccessToken {
  return {
    type: ReduxActionTypes.SET_ACCESS_TOKEN,
    data: {
      accessToken
    }
  };
}

export function setCommitteeData(committee: CommitteeDTO): ReduxSetCommitteeData {
  return {
    type: ReduxActionTypes.SET_COMMITTEE_DATA,
    data: {
      committee
    }
  };
}

export function resetAuthData(): ReduxResetAuthData {
  return {
    type: ReduxActionTypes.RESET_AUTH_DATA
  };
}
