import * as React from 'react';
import { Button, Col, Form, message, Modal, Row, Input, Select, Icon } from 'antd';
import { connect } from 'react-redux';
import { WithUser, WithConnector } from '../../reducers/reducers';
import {
  setUserData,
  setAccessToken,
  setCommitteeData,
  setUsersData,
  SetUserDataProps,
  SetCommitteeDataProps,
  SetUsersDataProps,
  SetAccessTokenProps
} from '../../actions/action';
import { Connector } from '../../utils/Connector';
import CommitteeDTO from '../../dto/CommitteeDTO';
import { FormComponentProps } from 'antd/lib/form/Form';
import { Links } from '../../utils/Links';
import { RouterProps, RouteProps } from 'react-router';
import { PORTAL_NAME } from '../../utils/Constants';
import { getUserInfo, setUserInfo } from '../../utils/Utils';
import queryString from 'query-string';

const { Option } = Select;

interface LoginFormState {
  committees: CommitteeDTO[];
  isForMobile: boolean;
  loading: boolean;
}

interface LoginFormProps
  extends WithUser,
    WithConnector,
    SetUserDataProps,
    SetAccessTokenProps,
    SetCommitteeDataProps,
    SetUsersDataProps,
    FormComponentProps,
    RouterProps,
    RouteProps {}

class LoginForm extends React.Component<LoginFormProps, LoginFormState> {
  constructor(props: any) {
    super(props);
    let isForMobile = false;
    const { location } = this.props;
    if (location !== undefined) {
      const { search } = location;
      if (search !== undefined) {
        const value = queryString.parse(search);
        const isMobileValue = value.layout;
        isForMobile = isMobileValue === 'mobile';
      }
      this.state = {
        committees: [],
        isForMobile,
        loading: false
      };
    }
  }

  connector: Connector | undefined;

  componentDidMount() {
    const userInfo = getUserInfo();
    if (userInfo) {
      this.login(userInfo.id, userInfo.committeeId);
    }
    this.props.connector
      .getCommittees()
      .then((res) => {
        this.setState(
          {
            ...this.state,
            committees: res.data
          },
          () => {
            if (res.data && res.data[0]) {
              this.handleSelectChange(res.data[0].id);
              if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
                this.props.form.setFieldsValue({
                  username: 'f.marchetti',
                  password: 'default'
                });
              }
            }
          }
        );
      })
      .catch((err) => {
        console.log(err);
      });
  }

  handleSelectChange = (value: number) => {
    this.props.form.setFieldsValue({
      committeeId: value
    });
  };

  private buildCommitteesSelect = () => {
    const { committees } = this.state;
    if (committees.length === 0) {
      return <Icon type="loading" />;
    }

    const { getFieldDecorator } = this.props.form;
    return (
      <Form.Item>
        {getFieldDecorator('committeeId', {
          rules: [{ required: true, message: 'Selezionare il comitato' }]
        })(
          <Select onChange={this.handleSelectChange}>
            {committees.map((committee: CommitteeDTO) => (
              <Option key={committee.id} value={committee.id}>
                {committee.description}
              </Option>
            ))}
          </Select>
        )}
      </Form.Item>
    );
  };

  private login = (userId: number, committeeId: number) => {
    const { connector } = this.props;
    const { isForMobile } = this.state;
    connector
      .getUser(userId)
      .then((res) => {
        const user = res.data;
        const committee_users: Array<any> = user.committee_users;
        if (committee_users) {
          const founded = committee_users.find((element) => element.committeeId === committeeId);
          if (!founded || founded.disabled === 1) {
            message.error('Utente non abilitato al comitato selezionato');
          } else {
            this.props.setUserData(user, committeeId);
            this.props.setCommitteeData(founded.committee);
            this.props.connector
              .getUsers(committeeId)
              .then((uRes) => {
                message.success('Autenticato');
                this.props.setUsersData(uRes.data);
                this.props.history.replace(isForMobile ? Links.MOBILE_SERVICES : Links.DESKTOP_SERVICES);
              })
              .catch((uErr) => {
                message.error(`Si è verificato un errore nel recupero degli utenti`);
              });
          }
        } else {
          message.error('Utente non abilitato al comitato selezionato');
        }
      })
      .catch((err) => {
        console.log(err);
        message.error(`Si è verificato un errore. Contattare l'amministratore di sistema.`);
      });
  };

  private handleSubmit = (e: any) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.setState({ loading: true }, () => {
          const committeeId = values.committeeId;
          const { username, password } = values;
          this.props.connector
            .signin(username, password, committeeId)
            .then((res) => {
              const { id, committeeId } = res.data;
              setUserInfo(res.data);
              this.setState({ loading: false }, () => this.login(id, committeeId));
            })
            .catch((err) => {
              let errorMessage = 'Username e password non validi';
              const { response } = err;
              if (response) {
                const { data } = response;
                if (data) {
                  errorMessage = data.message;
                }
              }
              this.setState({ loading: false }, () => message.error(errorMessage));
              console.error(err);
            });
        });
      }
    });
  };

  public render() {
    const { getFieldDecorator } = this.props.form;
    const { committees } = this.state;
    return (
      <Modal visible={true} closable={false} confirmLoading={false} footer={null}>
        <div style={{ textAlign: 'right' }}>
          <b>{PORTAL_NAME}</b>
        </div>
        <Row gutter={8}>
          <Col span={24}>
            <img alt={''} src={process.env.PUBLIC_URL + '/red_cross_logo.png'} width="100%" />
          </Col>
        </Row>
        <Form onSubmit={this.handleSubmit} className="login-form">
          <Form.Item>
            {getFieldDecorator('username', {
              rules: [{ required: true, message: 'Username obbligatoria' }]
            })(<Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Digitare il proprio username" />)}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator('password', {
              rules: [{ required: true, message: 'Password obbligatoria' }]
            })(<Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="Digitare la propria password" />)}
          </Form.Item>
          {this.buildCommitteesSelect()}
          <Form.Item>
            <Button
              loading={this.state.loading}
              disabled={committees === null || committees === undefined || committees.length === 0}
              type="primary"
              htmlType="submit"
              style={{ width: '100%' }}
              className="login-form-button"
            >
              Log in
            </Button>
          </Form.Item>
        </Form>
      </Modal>
    );
  }
}

const WrappeLoginForm = Form.create({ name: 'normal_login' })(LoginForm);
const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    connector: state.appData.connector
  };
};
const LoginContainer = connect(mapStateToProps, {
  setUserData,
  setAccessToken,
  setCommitteeData,
  setUsersData
})(WrappeLoginForm);
export default LoginContainer;
