import * as React from 'react';
import { Menu, Icon } from 'antd';
import { Links } from '../../../../utils/Links';
import { RouterProps } from 'react-router';
import { MenuMode } from 'antd/lib/menu';

export interface RedCrossMenuProps extends RouterProps {
  isUserAdmin: boolean;
  menuMode: MenuMode;
  onClickMenu?: () => void;
}

class RedCrossMenu extends React.Component<RedCrossMenuProps> {
  onClickMenuItem = (link: string) => {
    const { history, onClickMenu } = this.props;
    if (onClickMenu) {
      onClickMenu();
    }
    history.push(link);
  };

  public render() {
    const { isUserAdmin, menuMode } = this.props;
    return (
      <Menu theme="dark" defaultSelectedKeys={['1']} mode={menuMode}>
        <Menu.Item key="1" onClick={() => this.onClickMenuItem(Links.DESKTOP_SERVICES)}>
          <Icon type="calendar" />
          <span>Gestione Servizi</span>
        </Menu.Item>
        {isUserAdmin ? (
          <Menu.Item key="2" onClick={() => this.onClickMenuItem(Links.DESKTOP_USERS)}>
            <Icon type="team" />
            <span>Gestione Utenti</span>
          </Menu.Item>
        ) : undefined}
        {isUserAdmin ? (
          <Menu.Item key="3" onClick={() => this.onClickMenuItem(Links.DESKTOP_VEHICLES)}>
            <Icon type="car" />
            <span>Gestione Veicoli</span>
          </Menu.Item>
        ) : undefined}
        <Menu.Item key="4" onClick={() => this.onClickMenuItem(Links.DESKTOP_PERSONAL_AREA)}>
          <Icon type="user" />
          <span>Area personale</span>
        </Menu.Item>
      </Menu>
    );
  }
}

export default RedCrossMenu;
