import LoggedUserInfoDTO from '../dto/LoggedUserInfoDTO';
import { LOGGED_USER_INFO_LOCAL_STORAGE_KEY } from './Constants';
import TripDTO from '../dto/TripDTO';
import UserDTO from '../dto/UserDTO';

export const setUserInfo = (userInfo: string) => {
  localStorage.setItem(LOGGED_USER_INFO_LOCAL_STORAGE_KEY, JSON.stringify(userInfo));
};

export const getUserInfo = (): LoggedUserInfoDTO | undefined => {
  const loggedUserInfo: string | null = localStorage.getItem(LOGGED_USER_INFO_LOCAL_STORAGE_KEY);
  if (loggedUserInfo) {
    const loggedUserInfoDto: LoggedUserInfoDTO = JSON.parse(loggedUserInfo);
    return loggedUserInfoDto;
  }
  return undefined;
};

export const resetUserInfo = () => {
  localStorage.removeItem(LOGGED_USER_INFO_LOCAL_STORAGE_KEY);
};

export const getTripsOfDate = (date: Date, trips: TripDTO[], user: UserDTO | undefined): TripDTO[] => {
  const tripsOfDate: TripDTO[] = trips
    .filter((trip: TripDTO) => {
      return new Date(trip.date).setHours(0, 0, 0, 0) === date.setHours(0, 0, 0, 0);
    })
    .filter((trip: TripDTO) => {
      if (user !== undefined && user.committeeUser !== undefined) {
        const occupation = user.committeeUser.occupation;
        if (occupation === 0) {
          // Se è dipendente, vede tutti i servizi
          return true;
        }
        // Volontario o altro, vede i servizi non destinati ai dipendenti
        return trip.type > 0;
      }
      return false;
    })
    .sort((trip1: TripDTO, trip2: TripDTO) => {
      const hour1Replaced = trip1.hour.split(':').join('');
      const hour2Replaced = trip2.hour.split(':').join('');
      return Number(hour1Replaced) - Number(hour2Replaced);
    });
  return tripsOfDate;
};
