import * as React from 'react';
import { Radio } from 'antd';
import { ViewModeType } from '../../../utils/Constants';

export interface ServiceViewModeProps {
  actualValue: ViewModeType;
  onChangeView: (viewMode: ViewModeType) => void;
}

class ServiceViewMode extends React.Component<ServiceViewModeProps> {
  public render() {
    const { actualValue, onChangeView } = this.props;
    return (
      <div style={{ textAlign: 'end', margin: 10 }}>
        Visualizzazione:
        <Radio.Group defaultValue={actualValue} size="default" onChange={(e: any) => onChangeView(e.target.value)} style={{ marginLeft: 5 }}>
          <Radio.Button value={ViewModeType.week}>Settimana</Radio.Button>
          <Radio.Button value={ViewModeType.month}>Mese</Radio.Button>
        </Radio.Group>
      </div>
    );
  }
}

export default ServiceViewMode;
