import * as React from 'react';
import { connect } from 'react-redux';
import VehicleDTO, { VehicleType } from '../../dto/VehicleDTO';
import { WithConnector, WithCommittee } from '../../reducers/reducers';
import { Spin, Button, Popconfirm, Tooltip, Icon, Row } from 'antd';
import { message, Table } from 'antd';
import 'react-tiny-fab/dist/styles.css';
import VehicleForm from './forms/VehicleForm';
import PlusFabButton from './base/PlusFabButton';

interface IVechiclesState {
  loading: boolean;
  showForm: boolean;
  selectedVehicle: VehicleDTO | undefined;
  vehicles: VehicleDTO[];
}

class Vehicles extends React.Component<WithConnector & WithCommittee, IVechiclesState> {
  columns = [
    {
      title: 'Targa',
      dataIndex: 'plate',
      key: 'plate'
    },
    {
      title: 'Sigla',
      dataIndex: 'sign',
      key: 'sign'
    },
    {
      title: 'Tipologia',
      dataIndex: 'type',
      key: 'type',
      render: (value: any, record: any) => <span>{VehicleType[value]}</span>
    },
    {
      title: 'Descrizione',
      dataIndex: 'description',
      key: 'description'
    },
    {
      title: 'Disabilitato',
      dataIndex: 'disabled',
      key: 'disabled',
      render: (value: any, record: any) => <p>{value === 0 ? undefined : <Icon type="close" />}</p>
    },
    {
      title: '',
      key: 'action',
      render: (text: any, record: VehicleDTO) => (
        <span>
          <Tooltip title="Modifica">
            <Button size="small" icon="edit" onClick={() => this._onEditVehicle(record)} />
          </Tooltip>
          <Popconfirm placement="topLeft" title={`Confermare l'eliminazione?`} onConfirm={() => this._onDeleteVehicle(record)} okText="Si" cancelText="No">
            <Button size="small" style={{ marginLeft: '1px' }} icon="delete" type="danger" />
          </Popconfirm>
        </span>
      )
    }
  ];

  emptyVehicle: VehicleDTO = {
    id: 0,
    type: 0,
    plate: '',
    sign: '',
    description: '',
    disabled: 0,
    committeeId: this.props.committee?.id || 0,
    committee: this.props.committee as any
  };

  state = {
    loading: true,
    showForm: false,
    selectedVehicle: undefined,
    vehicles: []
  };

  componentDidMount() {
    this.loadData();
  }

  private loadData = () => {
    const { committee } = this.props;
    if (committee !== undefined) {
      this.props.connector
        .getVehicles(committee.id)
        .then((res) => {
          this.setState({
            vehicles: res.data,
            loading: false
          });
        })
        .catch((err) => {
          this.setState({ vehicles: [], loading: false });
          console.error(err);
        });
    }
  };

  private _onClose = () => {
    this.setState({ showForm: false, selectedVehicle: undefined });
  };

  private _onEditVehicle = (selectedVehicle: VehicleDTO) => {
    this.setState({ selectedVehicle, showForm: true });
  };

  private _onDeleteVehicle = (vehicle: VehicleDTO) => {
    this.props.connector.deleteVehicle(vehicle).then((res) => {
      this.loadData();
    });
  };

  private _onSave = (vehicle: VehicleDTO) => {
    this.publishVehicle(vehicle);
  };

  private publishVehicle = (vehicle: VehicleDTO) => {
    this.props.connector
      .publishVehicle(vehicle)
      .then((res) => {
        message.success('Salvataggio completato');
        this.setState({ showForm: false, selectedVehicle: undefined }, () => {
          this.loadData();
        });
      })
      .catch((err) => {
        console.error(err);
        message.error('Si è verificato un errore nel salvataggio');
      });
  };

  private _onCreate = () => {
    this.setState({ selectedVehicle: this.emptyVehicle, showForm: true });
  };

  public render() {
    const { loading, vehicles } = this.state;
    return (
      <div>
        {loading ? (
          <div style={{ textAlign: 'center' }}>
            <Spin />{' '}
          </div>
        ) : (
          <Row>
            <Table columns={this.columns} pagination={{ pageSize: 10 }} dataSource={vehicles} />
            <VehicleForm visible={this.state.showForm} vehicle={this.state.selectedVehicle} onClose={this._onClose} onSave={this._onSave} />
            {this.state.showForm ? undefined : <PlusFabButton onClick={this._onCreate} />}
          </Row>
        )}
      </div>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => {
  return {
    connector: state.appData.connector,
    committee: state.appData.committee
  };
};
const VehiclesContainer = connect(mapStateToProps)(Vehicles);
export default VehiclesContainer;
