import * as React from 'react';
import { Months, WeekDays } from '../../../utils/Constants';
import { Row, Col, Card, Badge } from 'antd';

export interface DayOfWeekProps {
  selectedDate: Date;
  date: Date;
  todayTrips: number;
  onClickDayOfWeek: (date: Date) => void;
}

const today = new Date();

class DayOfWeek extends React.Component<DayOfWeekProps, any> {
  private renderCardHeaderDate(date: Date) {
    const dayOfMonth: number = date.getDate();
    const month: string = Months[date.getMonth()];
    return (
      <Row>
        <Col style={{ textAlign: 'left' }} span={24}>
          {dayOfMonth} {month.substring(0, 3)}
        </Col>
      </Row>
    );
  }

  private renderCardBodyWeekDay(date: Date) {
    const { todayTrips } = this.props;
    const weekDay: string = WeekDays[date.getDay()];
    return (
      <Row>
        <Col style={{ textAlign: 'left' }} span={24}>
          <b>{weekDay}</b>
          {todayTrips !== 0 ? <Badge count={todayTrips} style={{ backgroundColor: '#87D068', marginLeft: '3px' }} /> : undefined}
        </Col>
      </Row>
    );
  }

  public render() {
    const { selectedDate, date, onClickDayOfWeek } = this.props;
    const isSelected =
      date.getDate() === selectedDate.getDate() && date.getMonth() === selectedDate.getMonth() && date.getFullYear() === selectedDate.getFullYear();
    const isOld = date.getTime() < today.getTime();
    const isToday = date.getTime() === today.getTime();
    return (
      <Card
        onClick={() => onClickDayOfWeek(date)}
        size="small"
        title={this.renderCardHeaderDate(date)}
        className="current-day"
        style={{
          cursor: 'pointer',
          borderRadius: '10px',
          height: '90px',
          border: `2px solid ${isSelected ? '#1890ff' : '#e8e8e8'}`,
          backgroundColor: `${isToday ? '#e6f7ff' : isOld ? '#f2f4f5' : 'white'}`
        }}
      >
        {this.renderCardBodyWeekDay(date)}
      </Card>
    );
  }
}

export default DayOfWeek;
