import * as React from 'react';
import DayOfWeek from './DayOfWeek';
import { add } from 'date-fns';
import UserDTO from '../../../dto/UserDTO';
import TripDTO from '../../../dto/TripDTO';
import { getTripsOfDate } from '../../../utils/Utils';
import { Row, Col } from 'antd';

export interface DaysOfWeekProps {
  startDate: Date;
  rowIndex: number;
  selectedDate: Date;
  trips: TripDTO[];
  user: UserDTO | undefined;
  onClickDayOfWeek: (date: Date) => void;
}

class DaysOfWeek extends React.Component<DaysOfWeekProps, any> {
  private renderDaysOfWeek = () => {
    const { startDate, rowIndex, selectedDate, trips, user, onClickDayOfWeek } = this.props;
    const elements: any = [];
    for (let index = 0; index < 7; index++) {
      const uniqueIndex = index + rowIndex * 7;
      var actualDate = add(startDate, {
        days: uniqueIndex
      });
      const tripsOfDate = getTripsOfDate(actualDate, trips, user);
      elements.push(
        <Col span={3} key={uniqueIndex}>
          <DayOfWeek date={actualDate} selectedDate={selectedDate} onClickDayOfWeek={onClickDayOfWeek} todayTrips={tripsOfDate.length} />
        </Col>
      );
    }
    return elements;
  };

  public render() {
    return (
      <Row justify="center" type="flex">
        {this.renderDaysOfWeek()}
      </Row>
    );
  }
}

export default DaysOfWeek;
