import React from 'react';
import { Button, Row, Col, Tooltip, Layout } from 'antd';
import { PORTAL_NAME } from '../../../utils/Constants';
import { WithUser, WithCommittee } from '../../../reducers/reducers';

const { Header } = Layout;

export interface RedCrossHeaderProps extends WithUser, WithCommittee {
  onClickLogout: () => void;
}

class RedCrossHeader extends React.Component<RedCrossHeaderProps> {
  public render() {
    const { committee, user, onClickLogout } = this.props;
    return (
      <Header style={{ background: '#001529', maxHeight: '64px', width: '100%', padding: 0, position: 'fixed', zIndex: 10, left: 0 }}>
        <Row>
          <Col span={16} style={{ textAlign: 'left' }}>
            <span
              style={{
                color: 'white',
                marginLeft: '15px',
                fontStyle: 'bold',
                fontSize: 'larger'
              }}
            >
              {PORTAL_NAME}
            </span>
            -
            <span
              style={{
                color: 'white',
                marginLeft: '5px',
                fontStyle: 'italic'
              }}
            >
              {committee?.description}
            </span>
          </Col>
          <Col span={7} style={{ textAlign: 'right' }}>
            <span style={{ color: 'white', marginRight: '5px' }}>
              {user?.name} {user?.surname}
            </span>
          </Col>
          <Col span={1}>
            <Tooltip title="Logout" placement="bottom">
              <Button ghost icon="logout" style={{ height: '64px', width: '100%' }} onClick={onClickLogout} />
            </Tooltip>
          </Col>
        </Row>
      </Header>
    );
  }
}

export default RedCrossHeader;
