import * as React from 'react';
import moment from 'moment';
import { connect } from 'react-redux';
import { WithUser, WithConnector } from '../../reducers/reducers';
import { message } from 'antd';
import UserDTO from '../../dto/UserDTO';
import { RouterProps } from 'react-router';
import { SetUserDataProps, setUserData } from '../../actions/action';
import { createForm } from 'rc-form';
import { List, InputItem, Button, Picker, WhiteSpace, DatePicker, LocaleProvider } from 'antd-mobile';
import { PickerData } from 'antd-mobile/lib/picker/PropsType';
import ChangePasswordModal from '../common/ChangePasswordModal';
import enUS from 'antd-mobile/lib/locale-provider/en_US';

interface PersonalAreaMobileProps extends WithUser, WithConnector, SetUserDataProps, RouterProps {
  form: any;
}

interface PersonalAreaMobileState {
  passwordChangeFormVisible: boolean;
}

class PersonalAreaMobile extends React.Component<PersonalAreaMobileProps, PersonalAreaMobileState> {
  state = {
    passwordChangeFormVisible: false
  };

  private onSaveUser = (userToPublish: UserDTO) => {
    const { user } = this.props;
    this.props.connector
      .publishUser(userToPublish)
      .then((res) => {
        this.props.setUserData(userToPublish, user?.committeeUser.id || 0);
        message.success('Salvataggio completato');
      })
      .catch((err) => {
        console.error(`_onSave: error ${err}`);
        message.error('Si è verificato un errore nel salvataggio');
      });
  };

  private _onChangePassword = () => {
    this.setState({ passwordChangeFormVisible: true });
  };

  private _onPasswordChangeConfirm = (oldPassword: string, newPassword: string): void => {
    const { connector } = this.props;
    connector
      .modifyPassword(oldPassword || '', newPassword || '')
      .then((res: any) => {
        this.setState(
          {
            passwordChangeFormVisible: false
          },
          () => message.success('Password modificata correttamente')
        );
      })
      .catch((err: any) => {
        let errorMessage: string = 'Si è verificato un errore nella richiesta';
        const { response } = err;
        if (response && response.data && response.data && response.data.message) {
          errorMessage = response.data.message;
        }
        message.error(errorMessage);
      });
  };

  private _onPasswordChangeCancel = (e: React.MouseEvent<HTMLElement>): void => {
    this.setState({
      passwordChangeFormVisible: false
    });
  };

  private _onSubmit = () => {
    this.props.form.validateFields((error: any, values: any) => {
      let allErrors: string[] = [];
      if (error !== null && error !== undefined) {
        for (let key in error) {
          const propertyError: any = error[key];
          if (propertyError && propertyError.errors) {
            const { errors } = propertyError;
            if (errors.length > 0) {
              allErrors = [...allErrors, errors[0].message];
            }
          }
        }
      }
      if (allErrors.length > 0) {
        allErrors.map((allError: string) => message.warn(allError));
        return;
      }
      const { user } = this.props;
      const userToSave: UserDTO = {
        ...user,
        ...values,
        birth_date: values.birth_date ? moment(values.birth_date).format('YYYY-MM-DD') : undefined,
        gender: values.gender[0]
      };
      this.onSaveUser(userToSave);
    });
  };

  private getPickerGenderData = (): PickerData[] => {
    return [
      {
        value: 0,
        label: 'Maschio'
      },
      {
        value: 1,
        label: 'Femmina'
      }
    ];
  };

  private renderButtonChangePassword = () => {
    return (
      <Button onClick={this._onChangePassword} type="primary" size="small" style={{ width: '100%' }}>
        Modifica
      </Button>
    );
  };

  private getInitialValueBirthDate = () => {
    const { user } = this.props;
    return user?.birth_date ? moment(user.birth_date).toDate() : undefined;
  };

  public render() {
    const { user } = this.props;
    const { getFieldProps } = this.props.form;
    return (
      <LocaleProvider locale={enUS}>
        <div>
          <List renderHeader={() => 'Area personale'}>
            <InputItem
              {...getFieldProps('username', {
                initialValue: user?.username,
                rules: [{ required: true, message: 'Specificare il nome utente' }]
              })}
              disabled
              placeholder="Username"
            >
              Username
            </InputItem>
            <div style={{ width: '100%' }}>
              <div style={{ display: 'table-cell' }}>
                <InputItem
                  type="password"
                  disabled
                  {...getFieldProps('password', {
                    initialValue: `12345678`,
                    rules: [{ required: true, message: 'Specificare la password' }]
                  })}
                  placeholder="Password"
                >
                  Password
                </InputItem>
              </div>
              <div style={{ display: 'table-cell' }}>{this.renderButtonChangePassword()}</div>
            </div>
          </List>
          <WhiteSpace />
          <List>
            <InputItem
              {...getFieldProps('name', {
                initialValue: user?.name,
                rules: [{ required: true, message: 'Specificare il Nome' }]
              })}
              clear
              placeholder="Digitare il nome"
            >
              Nome
            </InputItem>
            <InputItem
              {...getFieldProps('surname', {
                initialValue: user?.surname,
                rules: [{ required: true, message: 'Specificare il Cognome' }]
              })}
              clear
              placeholder="Digitare il cognome"
            >
              Cognome
            </InputItem>
            <DatePicker
              minDate={new Date(1900, 1, 1)}
              maxDate={new Date()}
              mode="date"
              okText="Conferma"
              dismissText="Annulla"
              {...getFieldProps('birth_date', {
                initialValue: this.getInitialValueBirthDate(),
                rules: []
              })}
            >
              <List.Item arrow="horizontal">Data di nascita</List.Item>
            </DatePicker>
          </List>
          <Picker
            data={this.getPickerGenderData()}
            title="Seleziona il sesso"
            okText="Conferma"
            dismissText="Annulla"
            cols={1}
            {...getFieldProps('gender', {
              initialValue: [user?.gender],
              rules: [
                {
                  required: true,
                  message: 'Specificare il sesso'
                }
              ]
            })}
          >
            <List.Item arrow="horizontal">Sesso</List.Item>
          </Picker>
          <List>
            <InputItem
              {...getFieldProps('email', {
                initialValue: user?.email,
                rules: []
              })}
              clear
              placeholder="Specificare l'email personale"
            >
              Email
            </InputItem>
            <InputItem
              {...getFieldProps('cellphoneNumber', {
                initialValue: user?.cellphoneNumber,
                rules: []
              })}
              clear
              placeholder="Specificare numero di cellulare"
            >
              Cellulare
            </InputItem>
          </List>
          <List.Item>
            <Button type="primary" icon="check-circle-o" size="small" onClick={this._onSubmit}>
              Salva
            </Button>
          </List.Item>
          <ChangePasswordModal
            changePasswordModalVisible={this.state.passwordChangeFormVisible}
            onPasswordChangeCancel={this._onPasswordChangeCancel}
            onPasswordChangeConfirm={this._onPasswordChangeConfirm}
          />
        </div>
      </LocaleProvider>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    connector: state.appData.connector,
    committee: state.appData.committee
  };
};

const PersonalAreaMobileForm = createForm()(PersonalAreaMobile);

const PersonalAreaMobileContainer = connect(mapStateToProps, {
  setUserData
})(PersonalAreaMobileForm);

export default PersonalAreaMobileContainer;
