import * as React from 'react';
import { ViewModeType } from '../../../utils/Constants';
import { Row, Button, Col } from 'antd';
import DaysOfWeek from './DaysOfWeek';
import TripDTO from '../../../dto/TripDTO';
import UserDTO from '../../../dto/UserDTO';

export interface ServiceCalendarProps {
  viewMode: ViewModeType;
  firstDayOfView: Date;
  selectedDate: Date;
  trips: TripDTO[];
  user: UserDTO | undefined;
  onMoveViewRight: () => void;
  onMoveViewLeft: () => void;
  onClickDayOfWeek: (date: Date) => void;
}

class ServiceCalendar extends React.Component<ServiceCalendarProps, any> {
  private renderDays = () => {
    const { viewMode, firstDayOfView, selectedDate, trips, user, onMoveViewLeft, onMoveViewRight, onClickDayOfWeek } = this.props;
    const numRows: number = viewMode === ViewModeType.week ? 1 : 4;
    const rows = [];
    for (let rowIndex = 0; rowIndex < numRows; rowIndex++) {
      const row = (
        <Row justify="center" key={rowIndex} type="flex">
          <Col span={1} style={{ textAlign: 'right' }}>
            {rowIndex === 0 ? <Button icon="left" style={{ margin: 5 }} onClick={onMoveViewLeft} /> : undefined}
          </Col>
          <Col span={22}>
            <DaysOfWeek
              startDate={firstDayOfView}
              rowIndex={rowIndex}
              selectedDate={selectedDate}
              trips={trips}
              user={user}
              onClickDayOfWeek={onClickDayOfWeek}
            />
          </Col>
          <Col span={1} style={{ textAlign: 'left' }}>
            {rowIndex === 0 ? <Button icon="right" style={{ margin: 5 }} onClick={onMoveViewRight} /> : undefined}
          </Col>
        </Row>
      );
      rows.push(row);
    }

    return rows;
  };

  public render() {
    return <div>{this.renderDays()}</div>;
  }
}

export default ServiceCalendar;
