import * as React from 'react';
import { connect } from 'react-redux';
import { setUserData } from '../../actions/action';
import { WithUser, WithConnector, WithCommittee } from '../../reducers/reducers';
import TripDTO from '../../dto/TripDTO';

interface BookedServicesMobileProps extends WithUser, WithConnector, WithCommittee {
  bookedTrips: TripDTO[];
}

class BookedServicesMobile extends React.Component<BookedServicesMobileProps> {
  public render() {
    return <div>Servizi prenotati (ANCORA DA IMPLEMENTARE!)</div>;
  }
}

const mapStateToProps = (state: any) => {
  return {
    user: state.appData.user,
    connector: state.appData.connector,
    committee: state.appData.committee
  };
};

const ServicesMobileContainerMobile = connect(mapStateToProps, {
  setUserData
})(BookedServicesMobile);
export default ServicesMobileContainerMobile;
