import * as React from 'react';
import { Input, Modal, message } from 'antd';

export interface ChangePasswordModalProps {
  changePasswordModalVisible: boolean;
  onPasswordChangeConfirm: (oldPassword: string, newPassword: string) => void;
  onPasswordChangeCancel: (e: React.MouseEvent<HTMLElement>) => void;
}

interface ChangePasswordModalState {
  oldPassword?: string;
  newPassword?: string;
  newPasswordConfirm?: string;
}

class ChangePasswordModal extends React.Component<ChangePasswordModalProps, ChangePasswordModalState> {
  state = {
    oldPassword: undefined,
    newPassword: undefined,
    newPasswordConfirm: undefined
  };

  private onChangeOldPassowrd = (e: any) => {
    this.setState({ oldPassword: e.target.value });
  };

  private onChangeNewPassword = (e: any) => {
    this.setState({ newPassword: e.target.value });
  };

  private onChangeNewPasswordConfirm = (e: any) => {
    this.setState({ newPasswordConfirm: e.target.value });
  };

  private handlePasswordChangeConfirm = (e: React.MouseEvent<HTMLElement>): void => {
    const { oldPassword, newPassword, newPasswordConfirm } = this.state;
    if (!oldPassword || !newPassword || !newPasswordConfirm) {
      message.error('Completare correttamente i campi');
      return;
    }
    if (newPassword !== newPasswordConfirm) {
      message.error('Digitare correttamente la nuova password');
      return;
    }
    if (newPassword === newPasswordConfirm) {
      message.error('La nuova password non può coincidere con la vecchia password');
      return;
    }

    this.props.onPasswordChangeConfirm(oldPassword || '', newPassword || '');
  };

  public render() {
    const { changePasswordModalVisible, onPasswordChangeCancel: handlePasswordChangeCancel } = this.props;
    return (
      <Modal
        title="Modifica password"
        visible={changePasswordModalVisible}
        onOk={this.handlePasswordChangeConfirm}
        onCancel={handlePasswordChangeCancel}
        cancelText="Annulla"
        okText="Conferma"
      >
        <div>
          <Input.Password placeholder="Inserisci password corrente" onChange={this.onChangeOldPassowrd} />
          <Input.Password style={{ marginTop: '5px' }} placeholder="Inserisci nuova password" onChange={this.onChangeNewPassword} />
          <Input.Password style={{ marginTop: '5px' }} placeholder="Conferma nuova password" onChange={this.onChangeNewPasswordConfirm} />
        </div>
      </Modal>
    );
  }
}

export default ChangePasswordModal;
