import React from 'react';
import { message } from 'antd';
import { add, format } from 'date-fns';
import TripDTO from '../../dto/TripDTO';
import { connect } from 'react-redux';
import { WithUser, WithConnector, WithCommittee, WithUsers } from '../../reducers/reducers';
import VehicleDTO from '../../dto/VehicleDTO';
import TripUsersDTO from '../../dto/TripUsersDTO';
import PlusFabButton from './base/PlusFabButton';
import TripForm from './forms/TripForm';
import DuplicateTripForm, { IDuplicateTripItem } from './forms/DuplicateTripForm';
import { ViewModeType } from '../../utils/Constants';
import ServiceCalendar from './base/ServiceCalendar';
import ServiceViewMode from './base/ServiceViewMode';
import ServiceTimeline from './base/ServiceTimeline';

interface ServicesState {
  loading: boolean;
  selectedDate: Date;
  firstDayOfView: Date;
  viewMode: ViewModeType;
  trips: TripDTO[];
  vehicles: VehicleDTO[];
  showForm: boolean;
  showDuplicateTripForm: boolean;
  selectedTrip: TripDTO | undefined;
  rolesVisible: Array<boolean>;
}

const today = new Date();

class Services extends React.Component<WithUser & WithConnector & WithCommittee & WithUsers, ServicesState> {
  state = {
    loading: false,
    selectedDate: today,
    firstDayOfView: today,
    viewMode: ViewModeType.week,
    trips: [],
    vehicles: [],
    showForm: false,
    showDuplicateTripForm: false,
    selectedTrip: undefined,
    rolesVisible: []
  };

  componentDidMount() {
    this.loadData();
  }

  private getEmptyTrip = (): TripDTO => {
    const { vehicles } = this.state;
    const emptyTrip: TripDTO = {
      id: 0,
      date: format(this.state.selectedDate, 'yyyy-MM-dd'),
      hour: '12:00:00',
      flgDestination: 0,
      flgAr: 0,
      siteDeparture: '',
      siteArrival: '',
      serviceType: '',
      patientData: undefined,
      type: 2,
      extraUsers: 1,
      flgNurse: 0,
      flgToNotify: 0,
      notes: undefined,
      committeeId: this.props.committee?.id || 0,
      vehicleId: vehicles.length !== 0 ? (vehicles[0] as any).id : 0,
      committee: undefined as any,
      vehicle: undefined as any,
      trip_users: []
    };
    return emptyTrip;
  };

  private loadData = () => {
    const { committee } = this.props;
    if (committee !== undefined) {
      const committeeId: number = committee.id;
      this.props.connector
        .getTrips(committeeId)
        .then((res) => {
          this.setState({
            trips: res.data
          });
          this.props.connector
            .getVehicles(committeeId)
            .then((vRes) => {
              this.setState({
                vehicles: vRes.data,
                loading: false
              });
            })
            .catch((vErr) => {
              this.setState({ vehicles: [], loading: false });
              console.error(vErr);
            });
        })
        .catch((err) => {
          this.setState({ trips: [], loading: false });
          console.error(err);
        });
    }
  };

  private _onChangeView = (viewMode: ViewModeType) => {
    this.setState({ viewMode });
  };

  private _onClickDayOfWeek = (date: Date) => {
    this.setState({ selectedDate: date });
  };

  private _onDeleteTrip = (trip: TripDTO) => {
    const tripId: number = trip.id;
    this.props.connector
      .deleteTripUsersByTrip(tripId)
      .then((res) => {
        this.props.connector
          .deleteTrip(tripId)
          .then((tRes) => {
            this.loadData();
          })
          .catch((tErr) => {
            console.log(`_onDeleteTrip error: ${tErr}`);
            message.error(`Si è verificato un errore durante l'eliminazione`);
            this.loadData();
          });
      })
      .catch((err) => {
        console.log(`_onDeleteTrip error: ${err}`);
        message.error(`Si è verificato un errore durante l'eliminazione`);
      });
  };

  private _onEditTrip = (trip: TripDTO) => {
    this.setState({ selectedTrip: trip, showForm: true });
  };

  private _onDuplicateTrip = (trip: TripDTO) => {
    this.setState({ selectedTrip: trip, showDuplicateTripForm: true });
  };

  private _onDeleteTripUser = (tripUser: TripUsersDTO) => {
    const tripUserId: number = tripUser.id;
    this.props.connector
      .deleteTripUsers(tripUserId)
      .then((res) => {
        this.loadData();
      })
      .catch((err) => {
        console.error(`_onDeleteTripUser: ${err}`);
        message.error(`Si è verificato un errore nell'eliminazione`);
      });
  };

  get isUserAdmin(): boolean {
    const { user } = this.props;
    if (user !== undefined && user.committeeUser !== undefined) {
      return user?.committeeUser.role < 2;
    }
    return false;
  }

  private _onConfirmRole = (role: number, trip: TripDTO, userId: number) => {
    const tripToPublish: TripUsersDTO = {
      id: 0,
      tripId: trip.id,
      userId,
      role
    };
    const { rolesVisible } = this.state;
    const newRolesVisible = rolesVisible.map((element: boolean) => false);
    this.props.connector
      .publishTripUser(tripToPublish)
      .then((res) => {
        message.success('Salvataggio completato');
        this.setState({ rolesVisible: newRolesVisible }, () => {
          this.loadData();
        });
      })
      .catch((err) => {
        console.log(err);
        message.error('Si è verificato un errore nel salvataggio');
      });
  };

  private _onRoleVisibleChange = (uniqueIndex: number, roleVisible: boolean) => {
    const newArray: Array<boolean> = [...this.state.rolesVisible];
    newArray[uniqueIndex] = roleVisible;
    this.setState({ rolesVisible: newArray });
  };

  private _onCreate = () => {
    this.setState({ selectedTrip: this.getEmptyTrip(), showForm: true });
  };

  private _onCloseTripForm = () => {
    this.setState({ showForm: false, selectedTrip: undefined });
  };

  private _onCloseDuplicateTripForm = () => {
    this.setState({ showDuplicateTripForm: false, selectedTrip: undefined });
  };

  private _onConfirmDuplicateTrip = async (trip: TripDTO, newDates: IDuplicateTripItem[]) => {
    let withError: boolean = false;
    for (let index = 0; index < newDates.length; index++) {
      const newDate: IDuplicateTripItem = newDates[index];
      const date: string = newDate.date.format('YYYY-MM-DD');
      const hour: string = newDate.hour.format('HH:mm');
      const newTrip: TripDTO = {
        ...trip,
        id: 0,
        date,
        hour
      };

      await this.props.connector
        .publishTrip(newTrip)
        .then((res) => {
          console.log('_onConfirmDuplicateTrip done');
        })
        // eslint-disable-next-line
        .catch((err) => {
          withError = true;
          console.error(`_onConfirmDuplicateTrip error for trip with date ${date} and hour ${hour}. Error: ${err}`);
        });
    }
    this.setState({ showDuplicateTripForm: false, selectedTrip: undefined }, () => {
      if (withError) {
        message.error(`Si sono verificati degli errori durante l'operazione`);
      } else {
        message.success('Operazione completata con successo');
      }
      this.loadData();
    });
  };

  private _onSave = (trip: TripDTO) => {
    this.props.connector
      .publishTrip(trip)
      .then((res) => {
        message.success('Salvataggio completato');
        this.setState({ showForm: false, selectedTrip: undefined }, () => {
          this.loadData();
        });
      })
      .catch((err) => {
        console.log(err);
        message.error('Si è verificato un errore nel salvataggio');
      });
  };

  private _onMoveViewRight = () => {
    const { viewMode, firstDayOfView } = this.state;
    var newFirstDayOfView = add(firstDayOfView, {
      days: viewMode === ViewModeType.week ? 7 : 28
    });
    this.setState({
      firstDayOfView: newFirstDayOfView,
      selectedDate: newFirstDayOfView
    });
  };

  private _onMoveViewLeft = () => {
    const { viewMode, firstDayOfView } = this.state;
    var newFirstDayOfView = add(firstDayOfView, {
      days: viewMode === ViewModeType.week ? -7 : -28
    });
    this.setState({
      firstDayOfView: newFirstDayOfView,
      selectedDate: newFirstDayOfView
    });
  };

  public render() {
    const { selectedTrip, showForm, showDuplicateTripForm, vehicles, viewMode, firstDayOfView, selectedDate, trips, rolesVisible } = this.state;
    const { user, users } = this.props;
    return (
      <div>
        <ServiceViewMode actualValue={viewMode} onChangeView={this._onChangeView} />
        <ServiceCalendar
          viewMode={viewMode}
          firstDayOfView={firstDayOfView}
          selectedDate={selectedDate}
          trips={trips}
          user={user}
          onMoveViewLeft={this._onMoveViewLeft}
          onMoveViewRight={this._onMoveViewRight}
          onClickDayOfWeek={this._onClickDayOfWeek}
        />
        <ServiceTimeline
          selectedDate={selectedDate}
          trips={trips}
          user={user}
          committeeUsers={users}
          rolesVisible={rolesVisible}
          onEditTrip={this._onEditTrip}
          onDuplicateTrip={this._onDuplicateTrip}
          onDeleteTrip={this._onDeleteTrip}
          onConfirmRole={this._onConfirmRole}
          onDeleteTripUser={this._onDeleteTripUser}
          onRoleVisibleChange={this._onRoleVisibleChange}
        />
        <TripForm
          visible={showForm}
          trip={selectedTrip}
          vehicles={vehicles.filter((vehicle: VehicleDTO) => !!!vehicle.disabled)}
          onClose={this._onCloseTripForm}
          onSave={this._onSave}
        />
        <DuplicateTripForm
          visible={showDuplicateTripForm}
          trip={selectedTrip}
          onClose={this._onCloseDuplicateTripForm}
          onConfirm={this._onConfirmDuplicateTrip}
        />
        {showForm || showDuplicateTripForm || (user && user.committeeUser.role > 1) ? undefined : <PlusFabButton onClick={this._onCreate} />}
      </div>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    connector: state.appData.connector,
    committee: state.appData.committee,
    users: state.appData.users
  };
};
const ServicesContainer = connect(mapStateToProps)(Services);
export default ServicesContainer;
